
<template>
  <div id="root">
    

<br />

<div class="card border-secondary mb-3 mx-auto w-75 mw-50" style="max-width:50em">
  
  <div class="card-header">

    
    <div class="btn-group" role="group" aria-label="Basic example">
      <button type="button" class="btn btn-outline-primary ">Save</button>
      <button type="button" class="btn btn-outline-primary">Load</button>
      <button type="button" class="btn btn-outline-info"
        v-on:click="show_extra = !show_extra" >Toggle
      </button>
      
        
      <button id="btnGroupDrop1" type="button" class="btn btn-outline-info dropdown-toggle " data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
        Options
      </button>

      <div class="dropdown-menu" aria-labelledby="btnGroupDrop1">
        <a class="dropdown-item" href="#">Action</a>
        <a class="dropdown-item" href="#">Another action</a>
        <a class="dropdown-item" href="#">Something else here</a>
        <div class="dropdown-divider"></div>
        <a class="dropdown-item icon-repeat" href="#"><i class="fa fa-trash"></i>  Remove finished</a>
      </div>

    </div>

      
    </div>

  <div class="card-body">
    <h4 class="card-title">
      <div v-show="show_extra"> <add-todo-comp v-on:add="addTodo" /> </div>
    </h4>
    <p class="card-text">

      <!-- the list itself -->
      <ul class="list-group ">

<!-- item-comp root is a <li>, i use this so it will play nice with bootstrap -->
        <item-comp :text="item.text" :done="item.done" :uid="index"
          v-for="(item,index) in todo_list" :key="item.text"
          @swiped="removeTodo(index)"
        />
          
      </ul>

    </p>
  </div>
</div>

  </div>
</template>

<!------------------------------------------------------------------------------------------->

<script>
import AddTodoComp from './AddTodoComp.vue';
import ItemComp from './ItemComp.vue';

export default {
  name: 'MainContainer', 
  components: { 
    AddTodoComp,
    ItemComp,
  },
  props: {
    msg: String,
    name: String,

    // example for a more complex validation of a prop object
    count: {
      type: Number,
      default: 0,
      required: false,
      validator: (value) => value >= 0
    }
  },

  data: ()=>{ return {
    show_extra: Boolean,
    todo_list:  [
      { text: "first item", done: true},
      { text: "second item", done: false},
      { text: "third item", done: false},
    ],
  }},

  methods : { 
    toggleshow_extra() {
      this.data.show_extra = !this.data.show_extra;
    },
    addTodo(str) {
      this.todo_list.push( {text: str, done:false} );
    },
    removeTodo(index) {
      this.todo_list.splice(index, 1);
    }
  }
}

</script>

