<!-- component for adding a new todo. includes button and text -->

<template>
  <div>

<div class="form-group ">

  <div class="input-group add-wrapper " >
    <!-- button -->
    <button type="button" 
      @click="onAdd" 
      class="btn btn-outline-success addnew fa fa-plus" >
    </button>
    <!-- text. bootstrap with col to look better -->
    <div class="col-8">
      <input type="text" placeholder="add todo.." id="addTextbox"
        class="form-control"
        autofocus 
        v-model="todoText"
        v-on:keyup.enter="onAdd" 
      >
    </div>
  </div>
  
</div>

  </div>
</template>

<!------------------------------------------------------------------------------------------->

<script>
export default {
  name: "AddTodoComp",
  data: () => {
    return {
      count: 0,
      todoText: ""
    };
  },
  methods: {
    onAdd() {
      this.$emit("add", this.todoText);
      // clear text after we send the add signal
      this.todoText = "";
    }
  }
};
</script>

<!------------------------------------------------------------------------------------------->

<style scoped>
.btn {
  /* necesary for some reason bootstrap changes it so it offset slightly */
  font-size: 1rem; 
}
</style>
